import React from "react";
import { createComponent } from "webiny-app";
import { ModalComponent } from "webiny-app-ui";
import EditorWidgetSettings from "./LayoutEditorWidgetSettings";

class LayoutWidgetSettingsDialog extends React.Component {
    render() {
        const {
            modules: { Modal, Button, Form },
            services: { cms },
            widget,
            onChange
        } = this.props;

        let editorWidget = cms.getLayoutEditorWidget(widget.type);

        return (
            <Modal.Dialog>
                <Form
                    model={widget.settings || {}}
                    onSubmit={model => this.props.hide().then(() => this.props.onChange(model))}
                >
                    {({ model, submit, Bind }) => (
                        <Modal.Content>
                            <Modal.Header title={"Layout widget settings"} onClose={this.props.hide} />
                            <Modal.Body>
                                {React.cloneElement(
                                    editorWidget.widget.renderSettings({
                                        EditorWidgetSettings,
                                        widget
                                    }),
                                    {
                                        Bind,
                                        settings: model,
                                        widget,
                                        onChange
                                    }
                                )}
                            </Modal.Body>
                            <Modal.Footer align="right">
                                <Button type="default" label="Cancel" onClick={this.props.hide} />
                                <Button type="primary" label="Save settings" onClick={submit} />
                            </Modal.Footer>
                        </Modal.Content>
                    )}
                </Form>
            </Modal.Dialog>
        );
    }
}

export default createComponent([LayoutWidgetSettingsDialog, ModalComponent], {
    modules: ["Form", "Modal", "Button"],
    services: ["cms"]
});
